@import "../vars.scss";
.page-top{
    color: #fff;
    padding: 24px 0;
    background-image: url(../img/top-bg.png);
    background-size: cover;
    background-position: center ;
    position: relative;
    &::before{
        content: '';
        position: absolute;
        left: 0;
        top: 0;
        width: 100%;
        height: 100%;
        background-color: rgba(0,0,0,.63);
        pointer-events: none;
    }
    &__title{
        margin-top: 27px;
        position: relative;
        z-index: 2;
        &:before{
            background-color: #fff;
            top: 23px;
        }
    }
}

.breadcrumbs {
    position: relative;
    z-index: 2;
    &__item {
        font-size: 16px;
        font-weight: 400;
        display: inline;
        &:not(:last-child){
            span{
                text-decoration: underline;
            }
            &:hover{
                color: $secondary;
            }
        }
        &:not(:first-child) {
            &:before {
                content: '-';
                margin: 0 16px;
            }
        }
    }
}


@media screen and (max-width: 767px){
    .page-top{
        padding-bottom: 32px;
        &__title{
            margin-top: 32px;
            &::before{
                top: 19px;
            }
        }
    }
}
