.catalog-item{
  padding: 64px 0 94px;
  border-bottom: 1px solid $secondary;
  &__inner{
    border: 1px solid $secondary;
    border-radius: 8px;
    padding: 40px;
    display: grid;
    grid-template-columns: 520px auto;
    gap: 40px;
  }
  &__title{
    font-size: 16px;
    margin-bottom: 16px;
  }
  &__group{
    &:not(:last-child){
      margin-bottom: 24px;
    }
  }
  &__bottom{
    margin-top: 35px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__gost{
    display: flex;
    align-items: center;
    img{
      margin-right: 7px;
      flex-shrink: 0;
    }
  }
  &__btn{
    width: 171px;
    height: 64px;
  }
}

.gallery{
  height: 432px;
  display: grid;
  grid-template-columns: calc(100% - 116px) 96px;
  gap: 20px;
  .swiper{
    width: 100%;
    &-slide{
      display: flex;
      align-items: center;
      justify-content: center;
      padding: 3px;
      img{
        height: 100%;
        max-width: 100%;
      }
    }
  }
  &__small{
    .swiper-slide{
      border-radius: 4px;
      border: 1px solid $secondary;
    }
    .swiper-slide-thumb-active{
      border-color: $primary;
    }
  }
}

.props{
  display: grid;
  grid-template-columns: 100%;
  gap: 15px;
  &__row{
    display: flex;
    align-items: center;
    justify-content: space-between;
    font-size: 16px;
    &:not(:last-child){
      padding-bottom: 15px;
      font-size: 16px;
      border-bottom: 1px solid $secondary;
    }
  }
  &__title{
    color: #666666;
  }
  &__desc{
    text-align: right;
  }
}

.catalog-info{
  background-image: url(../img/info.png);
  background-size: cover;
  background-position: right top;
  padding: 96px 0;
  border-bottom: 1px solid $secondary;
  &__content{
    margin-top: 32px;
  }
  .body-text{
    span{
      display: block;
      line-height: inherit;
    }
  }
}

.collection{
  background-image: none;
  .catalog-card{
    height: 100%;
  }
}


@media screen and (max-width: 1450px){
  .catalog-item{
    &__inner{
      padding: 40px 32px;
      grid-template-columns: 440px auto;
    }
  }

  .catalog-info{
    padding: 64px 0;
  }
}


@media screen and (max-width: 992px){
  .catalog-item{
    padding: 40px 0 63px;
    &__inner{
      padding: 24px;
      grid-template-columns: 400px auto;
      gap: 20px;
    }
    &__bottom{
      display: block;
    }
    &__btn{
      margin-top: 24px;
      width: 100%;
    }
    &__title{
      margin-bottom: 11px;
    }
  }

  .props{
    gap: 7px;
    &__row{
      &:not(:last-child){
        padding-bottom: 7px;
      }
    }
  }
}


@media screen and (max-width: 870px){
  .catalog-item{
    &__inner{
      grid-template-columns: 100%;
      gap: 24px;
      padding: 32px;
    }
    &__group{
      &:not(:last-child){
        margin-bottom: 20px;
      }
    }
    &__bottom{
      margin-top: 27px;
      display: flex;
    }
    &__btn{
      width: 171px;
      margin: 0;
    }
  }
}


@media screen and (max-width: 576px){
  .catalog-item{
    &__inner{
      padding: 24px;
    }
    &__bottom{
      display: block;
    }
    &__btn{
      margin-top: 16px;
      width: 100%;
    }
  }

  .gallery{
    height: 320px;
  }
}
