.footer{
  padding: 40px 0;
  color: #fff;
  background-color: $primary;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    width: 141px;
    margin-right: 20px;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__item{
    display: flex;
    align-items: center;
    &-icon{
      flex-shrink: 0;
      margin-right: 11px;
    }
  }
  &__address{
    font-size: 16px;
  }
  &__phone,
  &__mail{
    font-size: 24px;
    margin-left: 20px;
  }
  &__link{
    white-space: nowrap;
    &:hover{
      color: $secondary;
      path{
        fill: $secondary;
      }
    }
  }
  &__socials{
    display: flex;
    align-items: center;
    margin: 0 20px;
    &-item{
      font-size: 16px;
      display: flex;
      align-items: center;
      &:not(:last-child){
        margin-right: 18px;
      }
    }
  }
  &__brevis{
    display: flex;
    align-items: center;
    font-size: 12px;
    line-height: 1.2;
    img{
      width: 41px;
      margin-right: 24px;
    }
  }
  &__bottom{
    margin-top: 40px;
    border: 1px solid rgba(#ffffff,.16);
    padding: 32px;
    display: flex;
  }
  &__col{
    &:not(:last-child){
      margin-right: 48px;
    }
  }
  &__title{
    font-size: 16px;
    margin-bottom: 12px;
  }
  &__links{
    padding-left: 11px;
    .footer__link{
      white-space: unset;
      font-size: 16px;
      display: block;
      &:not(:last-child){
        margin-bottom: 8px;
      }
    }
    &--cols{
      column-count: 2;
      column-gap: 55px;
    }
  }
}


@media screen and (max-width: 1600px){
  .footer{
    &__socials{
      &-item{
        .footer__item-icon{
          margin-right: 0;
        }
        span{
          display: none;
        }
      }
    }
  }
}


@media screen and (max-width: 1450px){
  .footer{
    &__mail,
    &__phone{
      font-size: 16px;
    }
    &__bottom{
      margin-top: 31px;
    }
    &__links{
      padding-left: 0;
      &--cols{
        column-count: 1;
      }
    }
  }
}


@media screen and (max-width: 992px){
  .footer{
    &__logo{
      width: 120px;
    }
    &__address{
      display: none;
    }
    &__mail{
      margin-left: 0;
    }
  }
}


@media screen and (max-width: 830px){
  .footer{
    padding: 32px 0;
    &__socials,
    &__phone,
    &__mail{
      display: none;
    }
    &__brevis{
      img{
        margin-right: 16px;
      }
    }
    &__bottom{
      display: none;
    }
  }
}


@media screen and (max-width: 460px){
  .footer{
    padding: 24px 0;
    &__top{
      display: block;
    }
    &__brevis{
      margin: 16px 0 0;
    }
  }
}
