.display1{
  font-size: 48px;
  font-weight: 700;
}

.display2{
  font-size: 32px;
  font-weight: 400;
}

.line{
  padding-left: 82px;
  position: relative;
  &::before{
    content: '';
    position: absolute;
    width: 56px;
    height: 2px;
    background-color: $primary;
    left: 0;
    top: 15px;
  }
}

.body-text,
.body-text-big{
  p{
    line-height: inherit;
    &:not(:last-child){
      margin-bottom: 20px;
    }
  }
}

.body-text{
  font-size: 16px;
  line-height: 150%;
}

.body-text-big{
  font-size: 24px;
  line-height: 133%;
}

.gray-text{
  color: $gray;
}

.mlist{
  li{
    line-height: inherit;
    display: flex;
    &::before{
      content: '';
      display: block;
      width: 4px;
      height: 4px;
      border-radius: 50%;
      background-color: #000;
      transform: translateY(8px);
      margin-right: 5px;
      flex-shrink: 0;
    }
    &:not(:last-child){
      margin-bottom: 16px;
    }
  }
}


@media screen and (max-width: 767px){
  .display1{
    font-size: 32px;
    line-height: 130%;
  }
}

@media screen and (max-width: 460px){
  .display1,
  .display2{
    font-size: 28px;
  }
  .body-text{
    font-size: 14px;
  }

  .line{
    padding-left: 0;
    &:before{
      display: none;
    }
  }
}
