@import 'vars';
html {
    width: 100%;
}

.wrapper {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    flex-shrink: 0;
    padding-top: 136px;
}

.content {
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}

body {
    font-family: $font1;
    font-weight: 400;
    color: $black;
    &.fixed-body {
        overflow-y: hidden;
    }
}

.container {
    max-width: 100%;
    width: 1464px;
    margin: 0 auto;
    padding: 0 32px;

}

a {
    &:hover {
        color: $primary;
    }
}

@media screen and (max-width: 1600px) {
    .container{
        width: 1280px;
    }
}

@media screen and (max-width: 1450px) {
    .container{
        width: 1056px;
    }
}

@media screen and (max-width: 1200px) {
    .wrapper{
        padding-top: 104px;
    }

    .container{
        width: 100%;
    }
}

@media screen and (max-width: 460px){
    .wrapper{
        padding-top: 83px;
    }

    .container{
        padding: 0 15px;
    }
}
