.logo{
  display: block;
  width: 141px;
  flex-shrink: 0;
  img,
  svg{
    width: 100%;
    height: auto;
    display: block;
  }
}

.header{
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 10;
  color: #fff;
  &__inner{
    padding: 28px 0 24px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__item{
    display: flex;
    align-items: center;
    &-icon{
      flex-shrink: 0;
      margin-right: 11px;
    }
  }
  &__address{
    font-size: 16px;
  }
  &__phone,
  &__mail{
    font-size: 24px;
  }
  &__link{
    &:hover{
      color: $secondary;
      path{
        fill: $secondary;
      }
    }
  }
  &__btn{
    border-radius: 8px;
    height: 48px;
    width: 210px;
  }
  &._open{
    .menu{
      opacity: 1;
      pointer-events: unset;
    }
    .sidebar__burger{
      z-index: 101;
    }
  }
  &._search-open{
    .header-search{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &--inner{
    color: $black;
    .header{
      &__logo{
        path{
          fill: $primary;
        }
      }
      &__address{
        path{
          fill: $primary;
        }
      }
      &__link{
        path{
          fill: $primary;
        }
        &:hover{
          color: $primary;
          path{
            fill: $primary;
          }
        }
      }
    }
    .nav{
      &__link{
        &:hover{
          color: $primary;
        }
      }
    }
    .menu{
      color: #fff;
      background-color: rgba(0,0,0,.97);
    }
    .sidebar{
      background-color: $black;
    }
  }
}

.nav{
  padding: 0 0 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  &__item{
    position: relative;
    &-show{
      font-size: 16px;
      display: flex;
      align-items: center;
      svg{
        margin-left: 10px;
      }
      &--active{
        &~.nav__item-hidden{
          opacity: 1;
          pointer-events: unset;
          transform: translateY(0);
        }
      }
      &:hover{
        cursor: pointer;
        color: $secondary;
        path{
          fill: $secondary;
        }
      }
    }
    &-hidden{
      min-width: 100%;
      position: absolute;
      top: 100%;
      left: 0;
      padding-top: 15px;
      opacity: 0;
      pointer-events: none;
      transform: translateY(40px);
      transition: all .3s;
      &-inner{
        box-shadow: 0 4px 16px rgba(0,51,153,.04),0 2px 2px rgba(0,51,153,.08);
        background-color: #fff;
        border-radius: 8px;
        min-width: 100%;
        padding: 4px 0;
      }
    }
  }
  &__link,
  &__sublink{
    font-size: 16px;
    &:hover{
      color: $secondary;
    }
  }
  &__sublink{
    display: block;
    color: $primary;
    padding: 8px 16px;
  }
}

.sidebar{
  position: fixed;
  top: 0;
  left: 0;
  width: 80px;
  height: 100vh;
  background-color: rgba($black,.8);
  z-index: 30;
  padding: 24px 16px;
  display: flex;
  align-items: center;
  flex-direction: column;
  &__burger{
    width: 24px;
    height: 15px;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 15;
    span{
      display: block;
      width: 100%;
      height: 1px;
      background: #fff;
      left: 0;
      transition: all .3s;
      &:nth-child(1){
        position: absolute;
        top: 0;
      }
      &:nth-child(3){
        position: absolute;
        bottom: 0;
      }
    }
    &--active{
      span{
        &:nth-child(1){
          transform: rotate(-45deg) translate(-5px,5px);
        }
        &:nth-child(2){
          transform: scale(0);
        }
        &:nth-child(3){
          transform: rotate(45deg) translate(-5px,-5px);
        }
      }
    }
  }
  &__logo{
    display: block;
    height: 141px;
    margin-top: 24px;
    img{
      height: 100%;
      width: auto;
      display: block;
    }
  }
  &__action{
    margin-top: 27px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__btn{
    &:not(:last-child){
      margin-bottom: 26px;
    }
    &:hover{
      path{
        fill: $secondary;
      }
    }
  }
  &__cart{
    position: relative;
    &-num{
      position: absolute;
      top: -7px;
      right: -7px;
      width: 16px;
      height: 16px;
      display: flex;
      align-items: center;
      justify-content: center;
      border-radius: 50%;
      background-color: $primary;
      color: #fff;
      font-size: 12px;
    }
  }
  &__consult{
    width: 48px;
    height: 232px;
    margin-top: 28px;
    position: relative;
    border: 2px solid $primary;
    background-color: transparent;
    span{
      white-space: nowrap;
      display: block;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%) rotate(-90deg);
    }
  }
}

.menu{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba($black,.87);
  z-index: 100;
  padding: 30px 0 40px;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  .sidebar__burger{
    position: fixed;
    top: 24px;
    left: 28px;
  }
  .container{
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__inner{
    flex-grow: 1;
    margin-top: 40px;
    position: relative;
    display: flex;
    flex-direction: column;
  }
  &__group{
    width: 360px;
    margin-bottom: 40px;
  }
  &__tab,
  &__link{
    opacity: 0.63;
    font-size: 32px;
    &:hover{
      opacity: 1;
      cursor: pointer;
      color: #fff;
    }
  }
  &__tab{
    display: flex;
    align-items: center;
    &--active{
      span{
        text-decoration: underline;
      }
    }
    svg{
      margin-left: 17px;
    }
  }
  &__content{
    position: absolute;
    width: calc(100% - 360px);
    right: 0;
    top: 0;
    display: none;
    padding-bottom:40px;
  }
  &__grid{
    display: grid;
    grid-template-columns: repeat(4, calc(25% - (45px / 4)));
    gap: 15px;
  }
  &__item{
    border-radius: 8px;
    border: 1px solid rgba(#fff,.16);
    min-height: 168px;
    padding: 16px 20px 20px;
    display: flex;
    align-items: center;
    flex-direction: column;
    text-align: center;
    &:hover{
      border-color: #fff;
      color: #fff;
    }
  }
  &__icon{
    height: 64px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 11px;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__contacts{
    margin-top: auto;
  }
  &__mail,
  &__phone{
    font-size: 24px;
    font-weight: 700;
    display: flex;
    align-items: center;
    margin-top: 32px;
    svg{
      margin-right: 19px;
    }
    &:hover{
      color: $secondary;
      path{
        fill: $secondary
      }
    }
  }
}

.search-toggler._disable{
  pointer-events: none;
}

.header-search{
  transition: all .3s;
  position: fixed;
  left: 80px;
  top: 0;
  background-color: #fff;
  padding: 12px 24px;
  width: 600px;
  max-width: 100%;
  display: flex;
  align-items: center;
  opacity: 0;
  pointer-events: none;
  .form-group{
    flex-grow: 1;
    input{
      padding: 0 20px;
      font-size: 18px;
      height: 48px;
    }
  }
  &__btn{
    margin-left: 12px;
    height: 48px;
    width: 210px;
    flex-shrink: 0;
  }
}


@media screen and (max-width: 1600px){
  .menu{
    &__grid{
      grid-template-columns: repeat(3, calc(33.33% - 10px));
    }
  }
}


@media screen and (max-width: 1450px){
  .header{
    &__phone,
    &__mail{
      font-size: 16px;
    }
  }

  .menu{
    &__grid{
      grid-template-columns: repeat(2, calc(50% - 7.5px));
    }
  }
}


@media screen and (min-width: 1200px){
  @media screen and (max-height: 660px) {
    .sidebar{
      &__logo{
        height: 110px;
        margin-top: 12px;
      }
      &__consult{
        margin-top: auto;
      }
      &__action{
        margin: 12px 0;
      }
      &__btn{
        &:not(:last-child){
          margin-bottom: 12px;
        }
      }
    }
  }

  @media screen and (max-height: 550px){
    .sidebar{
      &__logo{
        display: none;
      }
      &__action{
        margin-top: 24px;
      }
    }
  }
}


@media screen and (max-width: 1200px){
  .header{
    &__inner{
      padding-left: 46px;
      padding-bottom: 28px;
    }
    &--inner{
      .sidebar{
        background-color: transparent;
        color: #fff;
        &__burger{
          span{
            background-color: $primary;
          }
        }
      }
      &._open{
        color: #fff;
        .menu .sidebar__burger{
          span{
            background-color: #fff;
          }
        }
      }
    }
  }

  .nav{
    display: none;
  }

  .sidebar{
    position: absolute;
    padding: 0;
    background-color: transparent;
    height: unset;
    width: unset;
    left: 32px;
    top: 42px;
    &__action,
    &__logo,
    &__consult{
      display: none;
    }
  }

  .menu{
    background: #000;
    &__top{
      display: flex;
      align-items: center;
      justify-content: space-between;
    }
    .sidebar__burger{
      position: relative;
      top: unset;
      left: unset;
    }
  }

  .header-search{
    left: 0;
    top: 100px;
    padding: 10px 32px;
    &__btn{
      width: 90px;
    }
  }
}


@media screen and (max-width: 992px){
  .header{
    &__address{
      display: none;
    }
  }

  .nav{
    display: none;
  }

  .menu{
    &__group{
      width: 100%;
      &:not(:last-child){
        margin-bottom: 24px;
      }
    }
    &__content{
      position: static;
      padding: 20px 0 12px 10px;
      width: unset;
    }
    &__grid{
      grid-template-columns: 100%;
      gap: 20px;
    }
    &__item{
      flex-direction: unset;
      min-height: unset;
      border:none;
      padding: 0;
    }
    &__title{
      font-size: 16px;
    }
    &__icon{
      margin: 0 20px 0 0;
      height: 35px;
      width: 45px;
      flex-shrink: 0;
    }
    &__link,
    &__tab{
      font-size: 24px;
    }
  }
}


@media screen and (max-width: 768px){
  .header{
    &__inner{
      padding-left: 33px;
    }
    &__mail{
      display: none;
    }
  }
}


@media screen and (max-width: 620px){
  .header{
    &__inner{
      padding-left: 36px;
    }
    &__phone{
      display: none;
    }
  }
}


@media screen and (max-width: 460px){
  .header{
    &__inner{
      padding: 20px 0 20px 32px;
    }
    &__logo{
      width: unset;
      flex-grow: 1;
      flex-shrink: unset;
      max-width: 133px;
    }
    &__btn{
      width: 180px;
      height: 43px;
      font-size: 14px;
      margin-left: 10px;
    }
  }

  .sidebar{
    left: 15px;
    top: 32px;
  }

  .header-search{
    top: 70px;
    padding: 10px 15px;
    .form-group{
      input{
        font-size: 16px;
      }
    }
    &__btn{
      width: 70px;
    }
  }
}
