.main{
  margin-top: -136px;
  color: #fff;
  overflow: hidden;
  &__inner{
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    max-width: 560px;
    padding: 120px 0;
  }
  .swiper{
    &-slide{
      background-size: cover;
      background-position: right center;
    }
    &-pagination{
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
      width: unset;
      bottom: 0;
      right: calc((100% - 1464px + 64px) / 2);
      left: unset;
      z-index: 2;
      &-bullet{
        width: unset;
        height: unset;
        margin: 26px 0 !important;
        font-size: 24px;
        opacity: .4;
        color: #ffffff;
        background-color: transparent;
        display: flex;
        align-items: center;
        justify-content: flex-end;
        transition: all .3s;
        &:after{
          content: '';
          display: block;
          width: 40px;
          height: 2px;
          background-color: #fff;
          margin-left: 24px;
          transition: all .3s;
        }
        &-active{
          font-size: 32px;
          opacity: 1;
          &:after{
            width: 56px;
          }
        }
      }
    }
  }
  &__desc{
    margin-top: 18px;
  }
  &__btn{
    margin-top: 17px;
    width: 191px;
    height: 64px;
  }
}

.mob-slider{
  overflow: hidden;
  @media screen and (min-width: 575px){
    .swiper{
      &-wrapper{
        transform: unset !important;
        display: grid;
      }
    }
  }
}

.products,
.factory,
.catalog{
  padding: 97px 0;
  border-bottom: 1px solid $secondary;
  &__items{
    margin-top: 65px;
  }
}

.about,
.factory,
.catalog{
  background-repeat: no-repeat;
  background-position: right top;
}

.factory{
  background-image: url(../img/factory.png);
  background-size: auto 100%;
  &__items{
    grid-template-columns: repeat(4, calc(25% - 18px));
    gap: 24px;
  }
}

.about,
.products,
.catalog{
  &__items{
    grid-template-columns: repeat(4, calc(25% - 30px));
    gap: 40px;
  }
}

.about{
  padding: 65px 0;
  background-image: url(../img/about.png);
  &__group{
    &:not(:last-child){
      margin-bottom: 95px;
    }
  }
  &__desc{
    margin-top: 47px;
  }
  &__items{
    display: grid;
    margin-top: 64px;
  }
  &__video{
    display: flex;
    align-items: center;
    margin-top: 65px;
    background-image: url(../img/about2.webp);
    background-size: cover;
    background-position: center;
    min-height: 96px;
    padding: 16px;
    color: #fff;
    border-radius: 8px;
    position: relative;
    overflow: hidden;
    &::before{
      content: '';
      position: absolute;
      left: 0;
      top: 0;
      width: 100%;
      height: 100%;
      background-color: rgba(0,0,0,.63);
      -webkit-transition: .3s;
      -o-transition: .3s;
      transition: .3s;
      pointer-events: none;
    }
    &:hover{
      color: #fff;
      span{
        text-decoration: none;
      }
    }
    &-icon{
      display: flex;
      align-items: center;
      justify-content: center;
      width: 64px;
      height: 64px;
      border: 4px solid #737fee;
      border-radius: 50%;
      background-color: $primary;
      flex-shrink: 0;
      margin-right: 16px;
      position: relative;
      z-index: 2;
    }
    span{
      position: relative;
      z-index: 2;
      text-decoration: underline;
    }
    br{
      display: none;
    }
  }
}

.power{
  padding: 32px 0 97px;
  border-bottom: 1px solid $secondary;
  &__title{
    margin-bottom: 65px;
  }
  &__items{
    margin-top: 40px;
    display: grid;
    grid-template-columns: repeat(3, calc(33.33% - (80px / 3)));
    gap: 40px;
  }
}

.catalog{
  background-image: url(../img/catalog-bg.png);
}

.req{
  padding: 97px 0;
  background-image: url(../img/req.png);
  background-size: cover;
  background-position: center;
  color: #ffffff;
  position: relative;
  .container{
    position: relative;
    z-index: 2;
  }
  &::before{
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    bottom: 0;
    right: 0;
    background-color: rgba(0,0,0,0.56);
    pointer-events: none;
  }
  &__title{
    &::before{
      background-color: #fff;
    }
  }
  &__form{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    gap: 40px;
    margin-top: 65px;
    .form-group{
      input{
        background-color: #fff;
        border: none;
      }
    }
  }
  &__btn{
    height: 64px;
  }
  &__hint{
    margin-top: 35px;
  }
}

.services{
  padding: 97px 0;
  &__items{
    margin-top: 65px;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 24px;
  }
}

.news,
.collection{
  padding: 97px 0;
  background-image: url(../img/news-bg.png);
  background-repeat: no-repeat;
  &__top{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__nav{
    display: flex;
    align-items: center;
  }
  &__slider{
    margin-top: 65px;
    overflow: hidden;
  }
  &__all{
    margin: 64px auto 0;
    width: 218px;
    height: 64px;
  }
  .swiper{
    &-wrapper{
      align-items: stretch;
    }
    &-slide{
      height: auto;
    }
    &-button{
      &-prev,
      &-next{
        position: static;
        margin: 0 0 0 8px;
        width: 32px;
        height: 32px;
      }
    }
  }
}

.contacts{
  position: relative;
  &__map{
    height: 488px;
  }
  &__card{
    border: 1px solid $secondary;
    border-radius: 8px;
    position: absolute;
    top: 50%;
    left: calc((100% - 1464px + 64px) / 2);
    transform: translateY(-50%);
    background-color: rgba(#fff,.87);
    min-width: 440px;
    max-width: calc(100% - 32px);
    padding: 32px;
    z-index: 2;
    overflow: hidden;
    &::before,
    &:after{
      content: '';
      position: absolute;
      background-color: $primary;
    }
    &:before{
      width: 64px;
      height: 64px;
      opacity: .23;
      top: -24px;
      right: -24px;
      border-radius: 20px;
    }
    &:after{
      width: 32px;
      height: 32px;
      top: -8px;
      right: -8px;
      border-radius: 10px;
    }
  }
  &__title{
    margin-bottom: 30px;
  }
  &__item{
    display: flex;
    align-items: center;
    margin-top: 25px;
    font-size: 24px;
  }
  &__icon{
    width: 20px;
    display: flex;
    justify-content: center;
    margin-right: 11px;
    flex-shrink: 0;
    svg{
      max-width: 100%;
      height: auto;
    }
  }
}


@media screen and (max-width: 1600px){
  .main{
    .swiper{
      &-pagination{
        right: calc((100% - 1280px + 64px) / 2);
      }
    }
  }

  .products,
  .about,
  .catalog{
    &__items{
      grid-template-columns: repeat(4, calc(25% - 24px));
      gap: 32px;
    }
  }

  .about{
    &__group{
      &:not(:last-child){
        margin-bottom: 50px;
      }
    }
  }

  .power{
    &__items{
      grid-template-columns: repeat(3, calc(33.33% - (64px / 3)));
      gap: 32px;
    }
  }

  .req{
    &__form{
      gap: 32px;
    }
  }

  .contacts{
    &__card{
      left: calc((100% - 1280px + 64px) / 2);
    }
  }
}


@media screen and (max-width: 1450px){
  .main{
    .swiper{
      &-pagination{
        right: calc((100% - 1056px + 64px) / 2);
      }
    }
  }

  .factory,
  .products{
    padding: 65px 0;
    &__items{
      margin-top: 41px;
    }
  }

  .factory,
  .about,
  .catalog{
    &__items{
      grid-template-columns: repeat(4, calc(25% - 12px));
      gap: 16px;
    }
  }

  .about{
    &__group{
      &:not(:last-child){
        margin-bottom: 65px;
      }
    }
    &__video{
      margin-top: 40px;
    }
  }

  .power{
    padding: 0 0 65px;
    &__title{
      margin-bottom: 40px;
    }
    &__items{
      grid-template-columns: repeat(3, calc(33.33% - (32px / 3)));
      gap: 16px;
    }
  }

  .catalog{
    padding: 65px 0;
    &__items{
      margin-top: 40px;
    }
  }

  .req{
    padding: 65px 0;
    &__form{
      gap: 15px;
      margin-top: 40px;
    }
    &__hint{
      margin-top: 20px;
    }
  }

  .services{
    padding: 65px 0;
    &__items{
      margin-top: 40px;
      gap: 16px;
    }
  }

  .collection,
  .news{
    padding: 65px 0;
    &__slider{
      margin-top: 40px;
    }
    &__all{
      margin-top: 32px;
    }
  }

  .contacts{
    &__card{
      left: calc((100% - 1056px + 64px) / 2);
    }
  }
}


@media screen and (max-width: 1200px){
  .main{
    margin-top: -104px;
    .swiper{
      &-pagination{
        right: 32px;
      }
    }
  }

  .contacts{
    &__card{
      left: 32px;
    }
  }
}


@media screen and (max-width: 992px){
  .mob-slider{

  }

  .mob-slider{
    .swiper-wrapper{
      display: flex;
    }
  }

  .factory,
  .products,
  .about,
  .catalog{
    &__items{
      display: flex;
      flex-wrap: wrap;
      gap: unset;
      width: calc(100% + 15px);
      margin-top: 26px;
    }
  }

  .factory,
  .products{
    .product-card,
    .factory-card{
      width: calc((100% / 3) - 15px);
      margin: 15px 15px 0 0;
      flex-grow: 1;
      max-width: calc(50% - 15px);
    }
  }

  .about{
    .about-card{
      margin: 15px 15px 0 0;
      width: calc(50% - 15px);
    }
  }

  .catalog{
    .catalog-card{
      margin: 15px 15px 0 0;
      width: calc((100% / 3) - 15px);
    }
  }

  .req{
    &__form{
      gap: unset;
      display: flex;
      flex-wrap: wrap;
      width: calc(100% + 15px);
      .form-group{
        width: calc((100% / 3) - 15px);
        margin: 0 15px 15px 0;
      }

    }
    .file-input,
    &__btn{
      width: calc(50% - 15px);
      margin-right: 15px;
      height: 48px;
    }
    &__hint{
      margin-top: 35px;
    }
  }

  .contacts{
    &__card{
      min-width: 245px;
      padding: 24px;
    }
    &__item{
      font-size: 16px;
    }
  }

  .services{
    @media screen and (min-width: 575px) {
      .swiper-wrapper {
        display: grid;
      }
    }
  }
}


@media screen and (max-width: 767px){
  .main{
    .container{
      padding-right: 144px;
    }
    &__inner{
      max-width: 340px;
      padding: 165px 0 80px;
    }
  }

  .factory{
    .factory-card{
      width: calc(50% - 15px);
    }
  }

  .products{
    .product-card{
      width: calc(50% - 15px);
    }
  }

  .power{
    &__items{
      grid-template-columns: 100%;
      margin-top: 32px;
    }
  }

  .catalog{
    .catalog-card{
      width: calc(50% - 15px);
    }
  }

  .contacts{
    &__card{
      min-width: 280px;
    }
  }
}

@media screen and (max-width: 575px){
  .main{
    .container{
      padding-right: 165px;
    }
    &__inner{
      padding-bottom: 104px;
    }
  }

  .mob-slider{
    .swiper-wrapper{
      flex-wrap: unset;
    }
  }

  .products,
  .factory,
  .about,
  .catalog,
  .services{
    &__items{
      width: 100%;
      //overflow-x: auto;
      //flex-wrap: unset;
      //width: calc(100% + 64px);
      //margin-left: -32px;
      //padding-left: 32px;
    }
    .service-card,
    .product-card,
    .factory-card,
    .about-card,
    .catalog-card{
      max-width: unset;
      width: 100%;
      flex-shrink: 0;
    }
  }

  .about{
    &__items{
      margin-top: 6px;
    }
    &__video{
      margin-top: 64px;
      flex-direction: column;
      padding: 16px 50px;
      text-align: center;
      &::before{
        //display: none;
      }
      br{
        display: unset;
      }
      &-icon{
        margin: 0 0 12px;
      }
    }
  }

  .req{
    &__form{
      width: 100%;
      .form-group{
        width: 100%;
        margin: 0 0 15px;
      }

    }
    .file-input,
    &__btn{
      width: 100%;
      margin: 0 0 15px;
      height: 48px;
    }
    &__hint{
      margin-top: 23px;
      line-height: 100%;
    }
  }

  .services{
    &__items{
      display: flex;
      gap:unset;
    }
  }

  .news,
  .collection{
    overflow: hidden;
    &__nav{
      display: none;
    }
    &__slider{
      overflow: unset;
    }
    &__all{
      margin-top: 40px;
    }
    .swiper-slide{
      width: 100%;
    }
  }
}


@media screen and (max-width: 460px){
  .main{
    margin-top: -83px;
    .container{
      padding-right: 85px;
    }
    .swiper{
      &-pagination{
        right: 15px;
        &-bullet{
          font-size: 18px;
          margin: 10px 0 !important;
          &:after{
            margin-left: 10px;
            width: 25px;
          }
          &-active{
            font-size: 24px;
            &:after{
              width: 35px;
            }
          }
        }
      }
    }
  }


  .contacts{
    &__card{
      left: 15px;
    }
  }
}
