@import '../vars';
.modal {
    background: rgba(#000, .87);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100vh;
    overflow-y: auto;
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 1000;
    opacity: 0;
    pointer-events: none;
    &--active{
        opacity: 1;
        pointer-events: unset;
    }
    &__dialog{
        border-radius: 8px;
        background-color: #fff;
        width: 360px;
        max-height: 100vh;
        overflow-y: auto;
        padding: 32px;
        position: relative;
    }
    &__top{
        display: flex;
        justify-content: space-between;
    }
    &__close{
        width: 20px;
        height: 20px;
    }
    &__form{
        display: grid;
        grid-template-columns: 1fr;
        gap: 16px;
        margin-top: 17px;
    }
    &__hint{
        font-size: 16px;
    }
    &__btn{
        width: 296px;
        height: 64px;
    }
}

.thank-modal{
    text-align: center;
    .modal{
        &__dialog{
            width: 620px;
        }
        &__btn{
            width: 129px;
        }
    }
}

@media screen and (max-width: 992px){
    .modal{
        &__dialog{
            padding: 40px 16px 70px;
        }
    }
}


@media screen and (max-width: 768px){
    .modal{
        &__dialog{
            height: 100vh;
        }
        &__form{
            &-grid{
                grid-template-columns: 100%;
            }
            &-full{
                grid-column: 1/2;
            }
        }
    }
}
