.service-card,
.factory-card{
  padding: 32px;
  border-radius: 8px;
  min-height: 320px;
  background-size: cover;
  background-position: center;
  display: flex;
  flex-direction: column;
  color: #fff;
  position: relative;
  overflow: hidden;
  &:hover {
    &:before {
      background-color: rgba($primary, .63);
    }
  }
  &:before{
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba($black,.63);
    transition: all .3s;
    pointer-events: none;
  }
  &__content{
    position: relative;
    z-index: 2;
    opacity: 0;
    pointer-events: none;
  }
  &__title{
    line-height: 100%;
    transition: all .3s;
    &--absolute{
      position: absolute;
      bottom: 0;
      left: 0;
      padding: 0 32px 32px;
      width: 100%;
    }
  }
  &__desc{
    margin-top: 9px;
    overflow: hidden;
    display: -webkit-box;
    -webkit-line-clamp:4;
    -webkit-box-orient: vertical;
  }
}

.factory-card{
  justify-content: space-between;
  &:hover{
    .factory-card__title--absolute{
      opacity: 0;
      pointer-events: none;
    }
    .factory-card__content{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__icon{
    display: flex;
    align-items: flex-end;
    height: 64px;
    position: relative;
    z-index: 2;
    margin-bottom: 25px;
    img{
      max-height: 100%;
    }
  }
  &__link{
    width: 135px;
    height: 32px;
    margin-top: 9px;
    border-radius: 4px;
    svg{
      margin-right: 11px;
    }
  }
}

.product-card,
.about-card,
.catalog-card,
.news-card{
  border: 1px solid $secondary;
  border-radius: 8px;
  display: flex;
  align-items: center;
  text-align: center;
  flex-direction: column;
}

.product-card,
.about-card{
  padding: 36px 32px;
  &__icon{
    height: 128px;
    display: flex;
    align-items: center;
    justify-content: center;
    svg,
    img{
      max-height: 100%;
      width: auto;
    }
  }
  &__title{
    margin-top: 21px;
  }
}

.product-card{
  min-height: 272px;
  &:hover{
    color: $black;
    .product-card__icon{
      path{
        fill: $primary;
      }
    }
  }
}

.about-card{
  position: relative;
  overflow: hidden;
  &::before,
  &:after{
    content: '';
    position: absolute;
    background-color: $primary;
  }
  &:before{
    width: 64px;
    height: 64px;
    opacity: .23;
    top: -24px;
    right: -24px;
    border-radius: 20px;
  }
  &:after{
    width: 32px;
    height: 32px;
    top: -8px;
    right: -8px;
    border-radius: 10px;
  }
  &__desc{
    margin-top: 18px;
  }
}

.power-card{
  border-radius: 8px;
  background-color: #3541b0;
  position: relative;
  overflow: hidden;
  padding: 32px 32px 24px;
  display: grid;
  grid-template-columns: auto 128px;
  gap: 25px;
  color: #fff;
  &:before{
    content: '';
    position: absolute;
    left: 0;
    top: 0;
    height: 100%;
    width: 100%;
    background-image: url(../img/power-bg.png);
    background-size: cover;
    pointer-events: none;
  }
  &__left,
  &__right{
    position: relative;
    z-index: 2;
    display: flex;
    flex-direction: column;
  }
  &__left{
    justify-content: space-between;
  }
  &__right{
    justify-content: flex-end;
    padding-left: 31px;
    position: relative;
    &::before{
      content: '';
      position: absolute;
      top: 0;
      left: 0;
      width: 2px;
      height: 100%;
      background-color: #fff;
    }
  }
  &__title{
    font-weight: 400;
    color: #737fee;
    line-height: 100%;
    margin-top: 14px;
  }
  &__name,
  &__size,
  &__type{
    line-height: 100%;
  }
  &__size{
    margin-bottom: 7px;
  }
  &__weight{
    font-size: 40px;
  }
  &__type{
    margin-top: 9px;
  }
}

.power-desc{
  border: 1px solid $secondary;
  border-radius: 8px;
  padding:32px;
  &__text{
    margin-top: 18px;
    p{
      margin-bottom: 0 !important;
    }
  }
}

.catalog-card{
  padding-top: 39px;
  background-color: #fff;
  &__img{
    position: relative;
    height: 210px;
    overflow: hidden;
    display: flex;
    justify-content: center;
    img{
      height: 100%;
      width: auto;
      object-fit: cover;
    }
  }
  &__content{
    width: 100%;
    margin-top: 22px;
    flex-grow: 1;
    display: flex;
    align-items: center;
    text-align: center;
    flex-direction: column;
    border-top: 1px solid $secondary;
    padding: 23px 20px 36px;
  }
  &__category{
    margin-bottom: 9px;
    &-icon{
      flex-shrink: 0;
      margin-right: 9px;
    }
  }
  &__has{
    display: flex;
    align-items: center;
    justify-content: center;
    i{
      width: 8px;
      height: 8px;
      background-color: #2bcb62;
      margin-right: 9px;
      flex-shrink: 0;
      border-radius: 50%;
      display: block;
    }
  }
  &__btn{
    max-width: 100%;
    width: 172px;
    height: 64px;
    margin-top: 10px;
  }
  &__link{
    margin-top: 20px;
  }
}

.service-card{
  justify-content: flex-end;
  &:hover{
    .service-card__title--absolute{
      opacity: 0;
      pointer-events: none;
    }
    .service-card__content{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__desc{
    -webkit-line-clamp:5;
  }
}

.news-card{
  text-align: left;
  &__img{
    padding-bottom: 56%;
    display: block;
    width: calc(100% + 2px);
    margin: -1px 0 0 -1px;
    position: relative;
    border-radius: 8px;
    overflow: hidden;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%,-50%);
    }
  }
  &__content{
    padding: 32px 27px;
    width: 100%;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
  }
  &__date{
    display: flex;
    align-items: center;
    img{
      margin-right: 9px;
    }
  }
  &__title{
    display: block;
    margin: 20px 0;
  }
  &__btn{
    width: 143px;
    height: 64px;
    margin-top: auto;
  }
}


@media screen and (max-width: 1600px){
  .product-card,
  .about-card{
    padding: 24px;
    &__title{
      margin-top: 17px;
    }
  }
  .product-card{
    min-height: 255px;
    &__title{
      br{
        display: none;
      }
      &[data-mob-text]{
        span{
          display: none;
        }
        &::before{
          content: attr(data-mob-text);
        }
      }
    }
  }

  .power-card{
    padding: 32px;
    gap: 24px;
    &__title,
    &__weight{
      font-size: 32px;
    }
    .body-text-big{
      font-size: 16px;
    }
  }
}


@media screen and (max-width: 1450px){
  .power-card{
    grid-template-columns: auto 120px;
    gap: 12px;
    &__right{
      padding-left: 17px;
    }
    &__title{
      font-size: 24px;
    }
    &__weight{
      font-size: 40px;
    }
    &__size{
      margin-bottom: 2px;
    }
    &__type{
      margin-top: 2px;
    }
    .body-text-big{
      margin-top: 0;
    }
  }

  .catalog-card{
    &__content{

    }
  }
}


@media screen and (max-width: 1200px){
  .power-card{
    padding: 24px;
  }
}


@media screen and (max-width: 992px){
  .factory-card{
    padding: 24px;
    &__icon{
      margin-bottom: 40px;
    }
  }

  .product-card{
    min-height: 224px;
    &__icon{
      height: 90px;
    }
  }

  .power-card{
    grid-template-columns: auto 75px;
    &__right{
      padding-left: 7px;
    }
    &__weight{
      font-size: 24px;
    }
    &__size{
      margin-bottom: auto;
      padding-top: 10px;
      padding-bottom: 8px;
    }
    &__type{
      margin-top: 15px;
    }
  }

  .power-desc{
    padding: 24px;
  }

  .service-card{
    padding: 24px;
  }

  .news-card{
    &__content{
      padding: 24px;
    }
    &__title{
      margin: 16px 0;
    }
  }
}


@media screen and (max-width: 767px){
  .power-card{
    grid-template-columns: auto 108px;
    &__right{
      padding-left: 30px;
    }
    &__title{
      font-size: 32px;
    }
    .body-text-big{
      font-size: 24px;
      margin-top: 5px;
    }
  }
}


@media screen and (max-width: 460px){
  .power-card{
    grid-template-columns: auto 90px;
    &__right{
      padding-left: 20px;
    }
    .body-text-big{
      font-size: 18px;
    }
  }
}
