@import 'vars';
@import 'components/_text.scss';
@import 'components/_buttons.scss';
@import 'components/_fonts.scss';
.form-group {
    position: relative;
    margin: 0;
    input {
        border-radius: 8px;
        border: 1px solid $secondary;
        padding: 0 10px 0 81px;
        height: 64px;
        width: 100%;
        font-size: 16px;
        background-color: transparent;
        color: $black;
        &::placeholder {
            color: $black;
        }
    }
    &__icon{
        position: absolute;
        padding-right: 14px;
        left: 22px;
        top: 50%;
        transform: translateY(-50%);
        border-right: 1px solid $secondary;
        display: flex;
        align-items: center;
        svg{
            width: 27px;
            height: auto;
        }
    }
}

.file-input{
    position: relative;
    height: 64px;
    input{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        z-index: 2;
        cursor: pointer;
        opacity: 0;
    }
    label{
        background-color: rgba(#fff,.16);
        border-radius: 8px;
        display: flex;
        align-items: center;
        width: 100%;
        height: 100%;
        justify-content: center;
        font-size: 16px;
        font-weight: 400;
        color: #ffffff;
    }
    svg{
        margin-right: 8px;
    }
    input{

    }
}

.tabs{
    display: grid;
    grid-template-columns: repeat(5,1fr);
    gap: 15px;
}

.tab{
    height: 64px;
    border: 1px solid #898c8e;
    border-radius: 8px;
    font-size: 16px;
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all .3s;
    cursor: pointer;
    &:hover,
    &--active{
        background-color: #fff;
        border-color: $primary;
        color: $primary;
    }
    &--active{
        pointer-events: none;
    }
    &__content{
        display: none;
        &--active{
            display: block;
        }
    }
}

.pagination{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-wrap: wrap;
    margin-top: 54px;
    &__item{
        margin: 10px 16px 0 0;
        width: 48px;
        height: 48px;
        border: 1px solid $primary;
        border-radius: 8px;
        display: flex;
        align-items: center;
        justify-content: center;
        font-size: 16px;
        color: $primary;
        &--active{
            color:#fff;
            background-color: $primary;
            pointer-events: none;
        }
    }
}

.check {
    position: relative;
    input {
        position: absolute;
        opacity: 0;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 2;
        cursor: pointer;
        &:checked+label {
            &::before {
                // background-color: $green;
                border: 3px solid #fff;
            }
        }
        &:disabled+label {
            color: #AAAAAA;
            pointer-events: none;
        }
    }
    label {
        padding-left: 40px;
        position: relative;
        min-height: 20px;
        font-size: 14px;
        line-height: 20px;
        cursor: pointer;
        &:before {
            content: '';
            position: absolute;
            z-index: 1;
            border-radius: 2px;
            background: rgba(255, 255, 255, 0.01);
            border: 1px solid rgba(255, 255, 255, 0.45);
            width: 20px;
            height: 20px;
            left: 0;
            top: 50%;
            transform: translateY(-50%);
            transition: all .100s;
        }
    }
}


@media screen and (max-width: 992px){
    .form-group{
        input{
            padding-left: 57px;
        }
        &__icon{
            padding-right: 5px;
            left: 14px;
        }
    }

    .tabs{
        gap: 7px;
    }

    .pagination{
        margin-top: 30px;
    }
}


@media screen and (max-width: 767px){
    .tabs{
        display: flex;
        flex-wrap: wrap;
        gap: unset;
    }
    .tab{
        height: 48px;
        flex-shrink: 0;
        padding: 0 8px;
        white-space: nowrap;
        margin: 0 8px 8px 0;
    }
}


@media screen and (max-width: 575px){
    .tab{
    }

    .pagination{
        display: none;
    }
}


@media screen and (max-width: 460px){
    .tab{
    }
}
