@import "../vars";
.mbtn {
    display: flex;
    align-items: center;
    justify-content: center;
    transition: all ease-out .225s;
    padding: 0;
    &:focus {
        box-shadow: none;
    }
}

.mbtn-primary{
    background-color: $primary;
    color: #fff;
    font-size: 16px;
    border-radius: 8px;
    &:hover{
        background-color: darken($primary,10%);
        color: #fff;
    }
}

.mbtn-white-outline{
    border: 1px solid #ffffff;
    color: #fff;
    &:hover{
        background-color: #fff;
        color: $black;
        path{
            fill: $black;
        }
    }
}

.blue-link{
    color: $primary;
    text-decoration: underline;
    &:hover{
        color: $primary;
        text-decoration: none;
    }
}
